  display: inline-block;
  width: 30px;
  height: 30px;
  position: absolute;
  top: 20px;
  right: 20px;
  background-color: transparent;
  outline: none;
  border: none;
}

.icon-close:before {
  content: '';
  width: 35px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 50%;
  border-top: 2px solid #717171;
  transform-origin: 0 center;
  transform: rotate(45deg);
}

