.startscreen {
  position: relative;
  width: 100%;
  margin-bottom: 70px;
  overflow: hidden;
}

.startscreen__slider, .startscreen__item,
.startscreen .slick-list,
.startscreen .slick-slide {
  position: relative;
  height: 85vh;
  min-height: 300px;
  font-size: 0;
  overflow: hidden;
  z-index: 5;
}

.startscreen__pic {
  position: absolute;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  transform: translate3d(-50%, -50%, 0);
}

.startscreen__content {
  position: relative;
  width: 60%;
  height: 100%;
  max-width: 760px;
  margin: 0 auto;
}

@media only screen and (max-width: 780px) {
  .startscreen__content {
    width: 90%;
  }
}

