  .startscreen__content {
    width: 70%;
  }
}

.startscreen__text {
  position: absolute;
  bottom: 90px;
  left: 0;
  transform: translate3d(0, 0, 0);
}

@media (orientation: landscape) {
  .startscreen__text {
    bottom: 80px;
  }
}

.startscreen__title {
  font-size: 60px;
  margin-bottom: 25px;
  line-height: 1.2;
